 .app-wrapper {
   margin:  auto;
   display: grid;
   width: 1200px;
   grid-template-areas:
 "h h"
 "n c";
     grid-template-rows: 60px 1fr;
     grid-template-columns: 2fr 10fr;
     grid-gap: 10px;
 }

 .app-wrapper-content{
     grid-area: c;
     background-color: rgba(5, 100, 94, 0.76);
 }
